.hero{margin-top:2rem;padding:0}.hero .image{position:relative;z-index:-1}.hero .image figure,.hero .image picture{width:100%;height:100%;aspect-ratio:1344/756}@media(max-width: 1024px){.hero .image figure,.hero .image picture{aspect-ratio:1/1.25}}.hero .image img{width:100%;height:100%;object-fit:cover;border-radius:1rem}.hero .image .placeholder{width:100%;height:100%;background:linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);border-radius:1rem;display:flex;align-items:center;justify-content:center;border:2px dashed rgba(26,80,119,.3)}.hero .image .placeholderContent{text-align:center;color:var(--primary)}.hero .image .placeholderContent span{display:block;font-size:18px;font-weight:600;margin-bottom:8px}.hero .image .placeholderContent small{font-size:14px;opacity:.7}.hero .content{position:relative;z-index:2;display:flex;flex-direction:column;gap:3rem;padding:3rem;border-radius:1rem;bottom:50%;background-color:var(--background);color:var(--black);margin-top:-15%}@media(max-width: 1200px){.hero .content{padding-inline:1rem;border-radius:0;margin-top:0;left:0;bottom:0;width:100%;max-width:100%;padding:1.5rem 0rem}}.hero .tagline{color:var(--accent-light);font-size:14px;font-weight:600;text-transform:uppercase;letter-spacing:.05em}.hero .title{color:var(--primary)}.hero .description{color:var(--primary);font-size:1.25rem}.hero .titleContainer{display:flex;flex-direction:column;gap:.5rem}.hero .text{display:flex;flex-direction:column;color:var(--primary);gap:1.5rem}.hero .cta{display:flex;flex-wrap:wrap;align-items:center;gap:1rem;width:100%;margin:0 auto}.hero .button{color:var(--accent);padding:.75rem 2rem;border-radius:10px;border:1px solid var(--accent);background-color:rgba(0,0,0,0);text-decoration:none;font-size:1rem;display:flex;align-items:center;gap:1.5rem;transition:all .3s ease}.hero .button svg{width:24px;height:24px}.hero .button svg path{stroke:var(--accent);fill:var(--white)}.hero .button span{white-space:nowrap}.hero .button:hover{background-color:var(--accent-dark);color:var(--white);transform:translateY(-2px);box-shadow:0 4px 12px rgba(0,0,0,.2)}.hero .button:hover svg path{stroke:var(--white);fill:var(--white)}@media(max-width: 1024px){.hero .button{padding-inline:1rem;padding-block:.5rem;width:100%}}.hero .contentBody h2{color:var(--primary);font-size:28px;font-weight:700;margin:48px 0 24px 0;line-height:1.3}.hero .contentBody h2:first-child{margin-top:0}.hero .contentBody p{color:var(--text-primary);font-size:16px;line-height:1.7;margin-bottom:24px;text-align:justify}.hero .contentBody ul{margin:24px 0;padding-left:24px}.hero .contentBody ul li{color:var(--text-primary);font-size:16px;line-height:1.7;margin-bottom:12px}.hero .contentBody ul li::marker{color:var(--primary)}.hero .contentBody ol{margin:24px 0;padding-left:24px}.hero .contentBody ol li{color:var(--text-primary);font-size:16px;line-height:1.7;margin-bottom:12px}.hero .contentBody ol li::marker{color:var(--primary);font-weight:600}.hero .contentBody strong{color:var(--primary);font-weight:600}.hero .contentBody em{color:var(--text-secondary);font-style:italic}@media(max-width: 991px){.hero .contentBody{padding:32px}.hero .contentBody h2{font-size:24px;margin:32px 0 20px 0}.hero .contentBody p{font-size:15px}.hero .contentBody ul li,.hero .contentBody ol li{font-size:15px}}@media(max-width: 767px){.hero .contentBody{padding:24px}.hero .contentBody h2{font-size:20px;margin:24px 0 16px 0}.hero .contentBody p{font-size:14px;margin-bottom:20px}.hero .contentBody ul,.hero .contentBody ol{padding-left:20px}.hero .contentBody ul li,.hero .contentBody ol li{font-size:14px;margin-bottom:10px}}